}

/*Time for mobile styling*/

.nav-burger {
  display: none;
}

.scroll-to-top {
  display: none;
  position: fixed;
  bottom: 80px;
  right: 20px;
  width: 44px;
  height: 44px;
  background-color: #a855f7;
  color: white;
  border: none;
  border-radius: 50%;
  font-size: 1rem;
  cursor: pointer;
  z-index: 100;
  align-items: center;
  justify-content: center;
  box-shadow: 0 4px 12px rgba(168, 85, 247, 0.4);
  opacity: 0;
  transition: opacity 0.3s ease;
  pointer-events: none;
}

.scroll-to-top.visible {
opacity: 1;
pointer-events: auto;
}

.content-wrapper {
  overflow-x: hidden;
}

@media (max-width: 768px) {
  .navbar {
    flex-wrap: wrap;
    padding: 15px 20px;
    width: 100%;
    box-sizing: border-box;
    position: sticky;
    top: 0;
    z-index: 50;
    opacity: 1;
    transition: none;
  }

  .nav-burger {
    display: flex;
    align-items: center;
    justify-content: center;
    background: none;
    border: 1px solid #a855f7;
    color: #a855f7;
    font-size: 1.3rem;
    width: 38px;
    height: 38px;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    margin-left: auto;
  }

  .nav-burger:hover {
    background-color: rgba(168, 85, 247, 0.15);
  }

  .nav-links {
    flex-direction: column;
    gap: 5px;
    width: 100%;
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.35s ease;
  }

  .nav-links.open {
    max-height: 400px;
  }

  .nav-links li {
    width: 100%;
  }

  .nav-links a {
    display: block;
    width: 100%;
    padding: 12px 16px;
    border-radius: 8px;
  }

  .nav-scrolled {
    opacity: 1;
  }

  .scroll-to-top {
    display: flex;
    bottom: 80px;
  }

  .dashboard-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 15px;
  }

  .dashboard-log-btn {
    width: 100%;
  }

  .dashboard-top-row {
    flex-direction: column;
  }

  .dashboard-latest-photo,
  .dashboard-no-photo {
    flex: none;
    width: 100%;
  }

  .dashboard-photo-img {
    aspect-ratio: 16/9;
  }

  .dashboard-stats-row {
    flex-wrap: wrap;
  }

  .dashboard-stat-tile {
    flex: 0 0 calc(50% - 8px);
  }

  .main-content {
    padding: 20px 15px;
  }

  .sets-header {
    display: none;
  }

  .set-row {
    flex-wrap: wrap;
    gap: 6px;
    padding-bottom: 12px;
    border-bottom: 1px solid #2d2b42;
    margin-bottom: 10px;
    align-items: center;
  }

  .set-col {
    order: 0;
    flex: 0 0 auto;
    width: auto;
  }

  .btn-remove-set {
    order: 1;
    flex: 0 0 auto;
    margin-left: auto;
    font-size: 1.2rem;
    padding: 5px 10px;
  }

  .set-row::after {
    content: '';
    order: 1;
    flex: 0 0 100%;
    height: 0;
  }

  .weight-col,
  .unit-col,
  .reps-col {
    flex: 1 1 0;
    width: auto;
    min-width: 0;
  }

  .cardio-checkbox-label input[type='checkbox'] {
    width: 24px;
    height: 24px;
  }

  .cardio-checkbox-label {
    font-size: 1.05rem;
  }

  .personal-best-banner {
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
  }

  .pb-date {
    margin-left: 0;
  }

  .progress-layout.has-photo {
    flex-direction: column;
  }

  .progress-layout.has-photo .progress-photo-section {
    flex: none;
    width: 100%;
  }

  .progress-photo {
    aspect-ratio: 16/9;
    object-fit: cover;
  }

  .progress-table-section {
    overflow-x: visible;
    position: relative;
  }

  .progress-table-section::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 40px;
    height: 100%;
    background: linear-gradient(to right, transparent, #1c1a2e);
    pointer-events: none;
    border-radius: 0 12px 12px 0;
  }

  .progress-table-wrapper {
    overflow-x: auto;
  }

  .progress-table {
    min-width: 500px;
  }

  .upload-form {
    flex-direction: column;
  }

  .upload-left {
    flex: none;
    width: 100%;
  }

  .upload-area {
    aspect-ratio: 16/9;
  }

  .upload-right {
    flex: none;
    width: 100%;
  }

  .btn-save-photo {
    width: 100%;
  }

  .compare-toggle {
    text-align: center;
  }

  .btn-compare-toggle {
    width: 100%;
  }

  .gallery-grid {
    grid-template-columns: repeat(2, 1fr);
  }

  .workout-actions {
    flex-direction: column;
  }

  .btn-edit, .btn-delete {
    width: 100%;
    text-align: center;
  }

  .cal-detail-body {
    flex-direction: column;
  }

  .cal-detail-photo-col {
    flex: none;
    width: 100%;
  }

  .cal-detail-photo, .cal-detail-no-photo {
    aspect-ratio: 16/9;
  }

  .cal-set-row {
    flex-wrap: wrap;
  }

  .site-footer {
    flex-direction: column;
    gap: 15px;
    text-align: center;
    padding: 20px 15px;
  }
}